<template>
    <yd-layout>
        <div class="otherAttend">
            <div class="search" :class="{active: searchFlag}">
                <input class="searchWord" type="text" v-on:input="search()" v-model="inputName" @focus="searchFlag = true" placeholder="戳我搜员工姓名哦">
                <span @click="searchFlag = false;inputName='';searchList=[]" class="cancel" v-show="searchFlag">取消</span>
                <div @click="inputName='';" class="close" v-show="searchFlag">
                    <img src="../../../static/images/workCenter/cancel.png" alt="">
                </div>
            </div>
            <!-- <div class="searchBox">
                <div class="searchContent">
                    <img src="../../../static/images/schedule/s_3.png" class="searchImg">
                    <input type="input" v-model="inputName" class="searchInput" placeholder="戳我搜员工姓名哦~" v-on:input="search()">
                </div>
            </div> -->
            <div class="addPeople" v-show="searchFlag==true">
                <yd-flexbox v-for="(item, index) in searchList" :key="index">
                    <div class="addName" v-text="item.empname"></div>
                    <div class="addDepart" v-text="splitArray(item.jobTitles)"></div>
                    <yd-flexbox-item>
                        <img src="../../../static/images/choose@2x.png" class="addImg1" @click="userid=item.empid;empname=item.empname;isShow()">
                        <img src="../../../static/images/tianjia@2x.png" class="addImg2" v-show="conNameList.indexOf(item.userId)==-1||contactList[conNameList.indexOf(item.userId)].id==0" @click="addCommCant(item.userId)">
                        <img src="../../../static/images/jianshao@2x.png" class="addImg2" v-show="conNameList.indexOf(item.userId)>=0&&contactList[conNameList.indexOf(item.userId)].id!=0" @click="delCommCant(item.userId)">
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
            <div v-show="searchFlag == false">
                <div class="mainContentFluid">
                    <div class="fluidWidth approveWidth">
                        <div class="approval">
                            常用联系人
                        </div>
                        <div class="name_circle">
                            <ul>
                                <li v-for="(item, index) in contactList" :key="index">
                                    <div class="lastname" @click="userid=item.empid;empname=item.empname;isShow()">
                                        {{item.empname.substring(0,1)}}
                                        <div class="nameClick">
                                        </div>
                                        <div class="imgClick">
                                        </div>
                                    </div>
                                    <div class="name" v-text="item.empname"></div>
                                </li>
                            </ul>
                            <div class="clear"></div>
                        </div>
                    </div>
                </div>
                <div class="schedule2" v-show="departList.length!=0">
                    <div class="swiper-container">
                        <div class="swiper-wrapper">
                            <div class="swiper-slide" :class="{'departActive':swiperFlag==item.orgname}" @click="swiperFlag=item.orgname;getorgAllUser(item.ORGID)" v-for="(item, index) in departList" :key="index" v-text="item.orgname"></div>
                        </div>
                    </div>
                </div>
                <div class="addPeople">
                    <yd-flexbox v-for="(item, index) in userList" :key="index">
                        <div class="addName" v-text="item.empname"></div>
                        <div class="addDepart" v-text="splitArray(item.jobTitles)" @click="textJob(splitArray(item.jobTitles))"></div>
                        <yd-flexbox-item>
                            <img src="../../../static/images/choose@2x.png" class="addImg1" @click="userid=item.empid;empname=item.empname;isShow()">
                            <img src="../../../static/images/tianjia@2x.png" class="addImg2" v-show="conNameList.indexOf(item.userId)==-1||contactList[conNameList.indexOf(item.userId)].id==0" @click="addCommCant(item.userId)">
                            <img src="../../../static/images/jianshao@2x.png" class="addImg2" v-show="conNameList.indexOf(item.userId)>=0&&contactList[conNameList.indexOf(item.userId)].id!=0" @click="delCommCant(item.userId)">
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <yd-popup v-model="jobShow" position="center" width="5.79rem">
            <div class="jobClass">{{jobText}}</div>
        </yd-popup>
        <yd-popup v-model="show" position="center" width="5.79rem">
            <div class="other">是否派其进行参会</div>
            <div class="otherBtn">
                <ul>
                    <li class="yes" @click="show=false;assignOther()">是</li>
                    <li class="no" @click="show=false">否</li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'otherAttend',
    data() {
        return {
            swiperFlag: '',
            departList: [],
            userList: [],
            contactList: [],
            show: false,
            userid: '',
            empname: '',
            deList: [],
            departNum: 0,
            conNameList: [],
            inputName: '',
            searchList: [],
            searchFlag: false,
            formList: [],
            shareId: '',
            shareUrl: '',
            meetEndTime: '',
            numIndex: '',
            jobShow: false,
            jobText: ''
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('派他人参会');
        vm.getAllCommCant();
        vm.getDeptAllOrg();
        vm.meetingData();
        vm.shareUrl = window.location.host;
        // vm.thisDepart();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            attendeeUserId: 'getAttendeeUserId'
        })
    },
    methods: {
        // 常用联系人
        getAllCommCant() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/getAllCommCant', {
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.contactList = res.jsonData;
                vm.conNameList = [];
                for (let i = 0; i < vm.contactList.length; i++) {
                    vm.conNameList.push(vm.contactList[i].userid);
                }
            });
        },
        // 查询科室
        getDeptAllOrg() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/getDeptAllOrg', {
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.departList = res.jsonData;
                for (let i = 0; i < vm.departList.length; i++) {
                    vm.deList.push(vm.departList[i].orgname);
                }
                vm.thisDepart();
            });
        },
        // 查询科室人员
        getorgAllUser(orgId) {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/getorgAllUser', {
                'orgId': orgId,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.userList = res.jsonData;
            });
        },
        // 显示本部门的成员
        thisDepart() {
            let vm = this;
            var promise = new Promise(function (resolve, reject) {
                vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/getorgAllUser', {
                    'name': vm.userInfo.ssotoken
                }, function (res) {
                    vm.userList = res.jsonData;
                    if (vm.userList[0].jobTitles.split('/')[0] != '公司领导') {
                        vm.swiperFlag = vm.userList[0].jobTitles.split('/')[1];
                    }
                    vm.departNum = vm.deList.indexOf(vm.swiperFlag);
                    resolve(true);
                });
            });
            promise.then(function (data) {
                if (data) {
                    vm.loadSwiper();
                }
            });
        },
        // swiper插件
        loadSwiper() {
            let vm = this;
            vm.$nextTick(() => {
                this.galleryTop = new Swiper('.swiper-container', {
                    initialSlide: vm.departNum,
                    slidesPerView: 'auto',
                    paginationClickable: true,
                    observer: true, // 修改swiper自己或子元素时，自动初始化swiper
                    observeParents: true // 修改swiper的父元素时，自动初始化swiper
                })
            });
        },
        // 指派他人参会
        assignOther() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/MeetManager/meet/assignOther', {
                'meetsid': vm.$route.params.meetingID,
                'othersId': vm.userid,
                'empname': vm.empname,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.share();
                vm.$dialog.toast({
                    mes: '指派成功'
                });
            });
        },
        meetingData() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/mobileMeetDetail', {
                'mid': vm.$route.params.meetingID,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.formList = res.jsonData;
                vm.meetEndTime = vm.formatDate(vm.formList.meetEndTime, 'hh:mm')
            });
        },
        share() {
            let vm = this;
            vm.get(vm.conf.uchatMessage + '/sendMessageApi', {
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'title': '会议参会确认',
                'url': `http://${vm.shareUrl}/detailsMeeting/${vm.$route.params.meetingID}/10`,
                'empcode': vm.userid,
                'summary': '会议主题：' + vm.formList.meetTitle + '\n' + '\n' + '会议时间：' + vm.formList.meetBeginTime + '-' + vm.meetEndTime + '\n' + '\n' + '会议地点：' + vm.formList.meetAddress
            }, function (res) {})
        },
        // 添加联系人
        addCommCant(userId) {
            let vm = this;
            if (vm.contactList.length > 11) {
                vm.$dialog.toast({
                    mes: '最多添加12个常用联系人',
                    timeout: 2000
                });
            } else {
                vm.post(vm.conf.portalMobile + '/API/MeetManager/meet/addCommCant', {
                    'others': userId,
                    'name': vm.userInfo.ssotoken
                }, function (res) {
                    vm.$dialog.toast({
                        mes: '成功添加常用联系人',
                        timeout: 2000,
                        callback: () => {
                            vm.getAllCommCant();
                        }
                    });
                });
            }
        },
        // 删除联系人
        delCommCant(userid) {
            let vm = this;
            let num = vm.contactList[vm.conNameList.indexOf(userid)].id;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/delCommCant', {
                'id': num,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                // for (let i = 0; i < vm.conNameList.length; i++) {
                //     if (vm.conNameList[i] == userid) {
                //         vm.conNameList.splice(i, 1);
                //     }
                // }
                console.log(vm.conNameList);
                vm.$dialog.toast({
                    mes: '成功删除常用联系人',
                    timeout: 2000,
                    callback: () => {
                        vm.getAllCommCant();
                    }
                });
            });
        },
        splitArray(jobTitles) {
            let vm = this;
            let arr = [];
            arr = jobTitles.split('/');
            for (let i = 0; i < arr.length; i++) {
                if (vm.swiperFlag == arr[i]) {
                    arr.splice(i, 1);
                }
            }
            return arr.join('/');
        },
        // 搜索接口
        search() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/MeetManager/meet/userSearch', {
                'key': vm.inputName,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.searchList = res.jsonData;
            });
        },
        // 判断是否已在参会人中
        isShow() {
            let vm = this;
            vm.attendeeUserId.userIdList = Array.from(new Set(vm.attendeeUserId.userIdList));
            for (let i = 0; i < vm.attendeeUserId.userIdList.length; i++) {
                if (vm.attendeeUserId.userIdList[i] == vm.userid) {
                    vm.numIndex = i;
                }
            }
            if (vm.attendeeUserId.userIdList[vm.numIndex] == vm.userid) {
                vm.$dialog.toast({
                    mes: '此人已在参会人中，无法指派',
                    timeout: 2000
                });
            } else {
                vm.show = true;
            }
        },
        textJob(job) {
            let vm = this;
            if (job.length > 11) {
                vm.jobShow = true;
                vm.jobText = job;
            }
        }
    }
}
</script>
<style lang="less" scoped>
.otherAttend {
    height: 100%;
    background-color: #f8f8f8;
}

.searchBox {
    width: 100%;
    height: 1.16rem;
    background-color: #fff;
    padding-top: .22rem;
    .searchContent {
        height: .72rem;
        background-color: #f0f0f0;
        margin-left: .43rem;
        margin-right: .28rem;
        border-radius: 5px;
        .searchImg {
            width: .4rem;
            height: .4rem;
            margin-top: .16rem;
            margin-left: .27rem;
            float: left;
        }
        .searchInput {
            font-size: inherit;
            display: block;
            border: none;
            float: left;
            height: .72rem;
            padding-left: .38rem;
            font-size: .28rem;
            color: #999;
            width: 5.7rem;
        }
        .searchInput::-webkit-input-placeholder {
            color: #999;
        }
    }
}

.mainContentFluid {
    margin-top: 0;
}

.setTop {
    margin-top: 0.2rem;
    height: .93rem;
    background-color: #f0f0f0;
    padding-left: .43rem;
    .yd-flexbox {
        line-height: .93rem;
        font-size: .3rem;
        color: #999;
        .depart1 {
            width: 2.11rem;
        }
        .depart2 {
            width: 2.25rem
        }
        .depart3 {
            width: 1.67rem
        }
        .active {
            color: #333;
        }
    }
}

.addPeople {
    background-color: #fff;
    .yd-flexbox {
        height: 1.18rem;
        color: #333;
        font-size: .3rem;
        padding-left: .43rem;
        border-bottom: 1px solid #efefef;
        .addName {
            width: 1.9rem;
            line-height: 1.18rem;
        }
        .addImg1 {
            width: .48rem;
            height: .48rem;
        }
        .addImg2 {
            width: .48rem;
            height: .48rem;
            margin-left: .3rem;
        }
        .addDepart {
            width: 3.4rem;
            line-height: 1.18rem;
            height: 1.18rem;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }
}

.name_circle ul li {
    width: 16.66%;
    margin-bottom: .2rem;
}

.swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: left;
    font-size: .3rem;
    width: 25%;
    color: #999999;
    line-height: .93rem;
}

.schedule2 {
    height: .93rem;
    padding-left: .43rem;
    margin-top: .2rem;
    position: relative;
    background-color: #f0f0f0;
}

.swiper-slide {
    margin-right: .28rem;
    height: .93rem;
}

.departActive {
    color: #333;
}

.other {
    border-bottom: none;
}

.jobClass {
    min-height: 0.93rem;
    line-height: 0.93rem;
    text-align: center;
    font-size: 0.3rem;
    border-bottom: 1px solid #e3e3e3;
}

.search {
    background: #fff;
    padding: .3rem;
    position: relative;
    &.active{
        padding: .3rem;
        .searchWord{
            width: 88%;
        }
    }
    .cancel{
        color: #59a5fd;
        font-size: .3rem;
        position: absolute;
        right: .26rem;
        top: .3rem;
        height: .7rem;
        line-height: .7rem;
        text-align: center;
    }
    .close{
        width: .66rem;
        height: .7rem;
        text-align: center;
        position: absolute;
        top: .3rem;
        right: 1.2rem;
        line-height: .65rem;
        img{
            width: .38rem;
            vertical-align: middle;
        }
    }
    .searchWord {
        background-color: #f3f3f3;
        background-position: .27rem;
        border-radius: .08rem;
        text-align: left;
        padding-left: .66rem;
        padding-right: .6rem;
    }
}

.searchWord {
    width: 100%;
    height: .7rem;
    padding-top: .17rem;
    padding-bottom: .22rem;
    border: 0;
    box-sizing: border-box;
    text-align: center;
    color: #2c3e50;
    outline: none;
    border-radius: 2px;
    margin-right: 10px;
    transition: border-color 0.2s ease;
    background: #fff url(../../../static/images/schedule/s_3.png) 47% center no-repeat;
    background-size: .26rem;
    padding-left: .56rem;
    font-size: .28rem;
    vertical-align: middle !important;
}
</style>


